.gnb__cont {
  background-color: white;
  .inner_cont {
    .main_menus {
      color: black;
      .center {
        & > div {
          & > div {
            & > a {
              text-decoration: none;
              color: black;
            }
          }
        }
      }
    }
  }
}
.full-size {
  width: 100%;
}
.container {
  width: 1100px;
  margin: 0 auto;
}

.svg-icon {
  filter: none;
}

.contents {
  margin-top: 100px;
  .sub_nav {
    height: 60px;
    background-color: #007c91;
    & > .container {
      height: inherit;
      display: flex;
      flex-direction: row;
      & > .icon {
        height: inherit;
        width: 70px;
        border-left: 1px solid #ccc;
        display: flex;
        justify-content: center;
        & > img {
          filter: invert(1);
          width: 30px;
        }
      }
      .sb_main_menu {
        height: inherit;
        width: 160px;
        border-left: 1px solid #ccc;
        display: flex;
        align-items: center;
        padding: 0px 20px 0px 20px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        & :first-child {
          color: white;
          font-family: SDGB;
        }
        & .icon > img {
          padding-top: 2px;
          height: inherit;
          width: 20px;
          filter: invert(1);
        }
      }
      .sb_sub_menu {
        padding-top: 10px;
        height: inherit;
        width: 160px;
        border-left: 1px solid #ccc;
        border-right: 1px solid #ccc;
        display: flex;
        align-items: center;
        padding: 0px 20px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        & :first-child {
          color: white;
          font-family: SDGB;
        }
        & .icon > img {
          padding-top: 4px;
          height: inherit;
          width: 20px;
          filter: invert(1);
        }
      }
    }
  }
  .greeting {
    height: 150px;
    .container {
      & > div {
        padding-top: 40px;
      }
    }
  }
  .img_back {
    height: 300px;
    background-image: url("../images/greeting.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
  .msg {
    height: 400px;
    .container {
      display: flex;
      height: 300px;
      flex-direction: column;
      justify-content: center;
      & > h2 {
        color: #007c91;
      }
      & > p {
        line-height: 1.5rem;
        color: #414141;
      }
    }
  }
}
.footer {
  width: 100%;
  background-color: #414141;
  height: 140px;

  .info {
    border-bottom: 1px solid #ccc;
    height: 70px;
    .container {
      height: 70px;
      display: flex;
      justify-content: space-between;
      line-height: 70px;
      color: white;
      .menu {
        display: flex;
        flex-direction: row;
        & div {
          font-size: 13px;
        }
        & div:nth-child(2) > span {
          border-left: 1px solid #ccc;
          border-right: 1px solid #ccc;
          padding: 0 20px;
        }
        & div + div {
          margin-left: 20px;
        }
      }
      .icon {
        display: flex;
        flex-direction: row;
        align-items: center;
        & > img {
          width: 40px;
          height: 40px;
          margin-left: 10px;
        }
      }
    }
  }
  .basic {
    .container {
      height: 70px;
      & > img {
        width: 60px;
        margin-left: -10px;
      }
    }
  }
}
